import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { useDispatch, useSelector } from 'react-redux';
import { selectTrainingFormat, setTrainingFormat } from './workspaceSlice';

const formats = [
  'amrap',
  'bodywt',
  'c2',
  'cfsb',
  'chipper',
  'couplet',
  'emom',
  'hybrid',
  'kb',
  'ladder',
  'metcon',
  'tabata',
  'timecap',
  'triplet',
  'strongman',
  'swim',
];
export default function SelectFormat() {
  const format = useSelector(selectTrainingFormat);
  const dispatch = useDispatch();

  const handleChange = (event: SelectChangeEvent) => {
    dispatch(setTrainingFormat(event.target.value as string));
  };

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id='training-format'>Training Format</InputLabel>
        <Select
          labelId='training-format'
          id='training-format'
          value={format}
          label='Training Format'
          onChange={handleChange}
          size='small'
        >
          {formats.map((format) => (
            <MenuItem value={format}>{format}</MenuItem>
          ))}
        </Select>
      </FormControl>
    </Box>
  );
}
