import { useSelector } from 'react-redux';
import { useSearchParams } from 'react-router-dom';
import { useGetRxdByIdQuery, useGetRxdQuery } from './api';
import { selectTrainingFormat } from './workspaceSlice';

export const useGetRxd = () => {
  const format = useSelector(selectTrainingFormat);

  const [searchParams] = useSearchParams();
  const id = searchParams.get('id');

  const rxdQuery = useGetRxdQuery(format, { skip: id != null });
  const rxdByIdQuery = useGetRxdByIdQuery(id || '', { skip: id == null });

  if (id == null) {
    return rxdQuery;
  } else {
    return rxdByIdQuery;
  }
};
