import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import { IRxdWorkout } from './api';

import { Alert, Skeleton, Typography } from '@mui/material';
import Timeline from '@mui/lab/Timeline';
import TimelineItem, { timelineItemClasses } from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import { WodBreadcrumbs } from './WodBreadCrumbs';

export default function Workout({
  data,
  format,
  refetch,
  isFetching,
  error,
}: {
  data?: IRxdWorkout;
  format?: string;
  refetch: () => void;
  isFetching: boolean;
  error: any;
}) {
  return (
    <Card elevation={0} sx={{ width: '100%' }}>
      <CardContent>
        <WodBreadcrumbs />

        {error && (
          <Alert severity='error'>
            Our service is currently unavailable =(
          </Alert>
        )}
        {isFetching || error ? (
          <React.Fragment>
            <Timeline
              sx={{
                [`& .${timelineItemClasses.root}:before`]: {
                  flex: 0,
                  padding: 0,
                },
              }}
              nonce={undefined}
              onResize={undefined}
              onResizeCapture={undefined}
            >
              <React.Fragment>
                {[1, 2, 3, 4].map((line, index) => (
                  <TimelineItem key={index}>
                    <TimelineSeparator>
                      <TimelineDot variant='outlined' />
                      <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent>
                      <Skeleton
                        animation='wave'
                        height={18}
                        width={Math.floor(Math.random() * 100) + 1 + '%'}
                      />
                    </TimelineContent>
                  </TimelineItem>
                ))}
              </React.Fragment>
            </Timeline>
          </React.Fragment>
        ) : (
          <>
            {data && data?.commands[0] && (
              <Typography sx={{ pt: 2 }} variant='h5'>
                {data.commands[0]}
              </Typography>
            )}
            <Timeline
              sx={{
                [`& .${timelineItemClasses.root}:before`]: {
                  flex: 0,
                  padding: 0,
                },
                m: 0,
              }}
              nonce={undefined}
              onResize={undefined}
              onResizeCapture={undefined}
            >
              <React.Fragment>
                {(data?.commands.slice(1) || []).map((line, index) => (
                  <TimelineItem key={index}>
                    <TimelineSeparator>
                      <TimelineDot variant='outlined' color='primary' />
                      <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent>
                      <Typography variant='h5' gutterBottom>
                        {line}
                      </Typography>
                    </TimelineContent>
                  </TimelineItem>
                ))}
              </React.Fragment>
            </Timeline>
          </>
        )}
      </CardContent>
    </Card>
  );
}
