import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../app/store';

interface WorkspaceState {
  trainingCategory: string;
  trainingFormat: string;
}
const initialState: WorkspaceState = {
  trainingCategory: 'RXD',
  trainingFormat: 'triplet',
};

export const workspaceSlice = createSlice({
  name: 'workspace',
  initialState,
  reducers: {
    setTrainingCategory: (state, action: PayloadAction<string>) => {
      state.trainingCategory = action.payload;
    },
    setTrainingFormat: (state, action: PayloadAction<string>) => {
      state.trainingFormat = action.payload;
    },
  },
});

export const { setTrainingCategory, setTrainingFormat } =
  workspaceSlice.actions;

export const selectTrainingFormat = (state: RootState) =>
  state.workspace.trainingFormat;
export const selectTrainingCategory = (state: RootState) =>
  state.workspace.trainingCategory;

export const workspaceReducer = workspaceSlice.reducer;
