import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { useDispatch, useSelector } from 'react-redux';
import { selectTrainingCategory, setTrainingCategory } from './workspaceSlice';

export const formats = ['RXD'];

export default function SelectCategory() {
  const category = useSelector(selectTrainingCategory);
  const dispatch = useDispatch();

  const handleChange = (event: SelectChangeEvent) => {
    dispatch(setTrainingCategory(event.target.value as string));
  };

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id='training-category'>Training Category</InputLabel>
        <Select
          labelId='training-category'
          id='training-category'
          value={category}
          label='Training Category'
          onChange={handleChange}
          size='small'
        >
          {formats.map((format) => (
            <MenuItem value={format}>{format}</MenuItem>
          ))}
        </Select>
      </FormControl>
    </Box>
  );
}
